import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';
import { ErrorCatcher } from '../models/errorCatcher';



@Injectable()

export class ErrorCatcherService {
    public url: string;
    public errorCatched: ErrorCatcher;
    constructor(
        private _http: HttpClient
    ) {
        this.url = Global.url + 'errors/'
    }

    saveError(errorCatched: ErrorCatcher): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(errorCatched);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url + 'save', params, { headers: headers });
    }

    workOnIt(errorCatched: ErrorCatcher): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(errorCatched);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url + 'put-me-on-it/'+ errorCatched._id, params, { headers: headers });
    }

    solvedError(errorCatched: ErrorCatcher): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(errorCatched);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url + 'i-got-it/' + errorCatched._id, params, { headers: headers });
    }

    getErrors(): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
        return this._http.get(this.url + 'errors', { headers: headers });
    }
}