import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { User } from '../models/user';
import { Global } from './global';

@Injectable()
export class UserService {
  public url: string;
  public identity;
  public token;
  public stats;
  constructor(
    private _http: HttpClient
  ) {
    this.url = Global.url + 'user/';
  }
  // Test de Register()
  // register(user_to_register) {
  //   console.log(user_to_register);
  //   console.log(this.url);
  // }

  // Asignaciones de Valores--------------------------------------------------//
  // -----Devolver Usuario-----------------------------------------------------//
  getIdentity() {
    const identity = JSON.parse(localStorage.getItem('identity'));
    if (identity != 'undefined') {
      this.identity = identity;
    } else if (identity == 'undefined') {
      this.identity = null;
    }
    return this.identity;
  }
  // -----Devolver Token-------------------------------------------------------//
  getToken() {
    const token = JSON.parse(localStorage.getItem('token'));
    if (token != 'undefined') {
      this.token = token;
    } else if (token == 'undefined') {
      this.token = null;
    }
    return this.token;
  }
  // -----Devolver Datos-------------------------------------------------------//
  getStats() {
    const stats = JSON.parse(localStorage.getItem('stats'));
    if (stats != 'undefined' || stats != null) {
      this.stats = stats;
    } else {
      this.stats = null;
    }
    return this.stats;
  }

  // Metodos------------------------------------------------------------------//
  // ------Post----------------------------------------------------------------//
  register(user: User): Observable<any> {
    const params = JSON.stringify(user);
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this._http.post(this.url + 'save', params, { headers });
  }
  registerSimple(user: User): Observable<any> {
    const params = JSON.stringify(user);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    return this._http.post(this.url + 'save-user', params, { headers });
  }
  login(user: User, gettoken = null): Observable<any> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    if (gettoken != null) {
      user.gettoken = gettoken;
    }
    let params = JSON.stringify(user);
    return this._http.post(this.url + 'login', params, { headers });
  }
  // ------Get-----------------------------------------------------------------//
  getCounters(userId = null): Observable<any> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    if (userId != null) {
      return this._http.get(this.url + 'contador/' + userId, { headers });
    } else {
      return this._http.get(this.url + 'contador', { headers });

    }
  }
  showRaw(): Observable<any> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    return this._http.get(this.url + 'show-raw', { headers });
  }
  getUsers(page = null): Observable<any> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    return this._http.get(this.url + 'users/' + page, { headers });
  }
  getUser(id): Observable<any> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    return this._http.get(this.url + 'show/' + id, { headers });
  }
  // ------Put-----------------------------------------------------------------//
  updateUser(user: User): Observable<any> {
    const params = JSON.stringify(user);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    return this._http.put(this.url + 'update/' + user._id, params, { headers });
  }
  updateCommons(user: User): Observable<any> {
    console.log(user);
    const params = JSON.stringify(user);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', this.getToken());
    return this._http.put(this.url + 'update-commons/' + user._id, params, { headers });
  }
  // ------Delete--------------------------------------------------------------//


}

