import { Component, OnInit, DoCheck } from '@angular/core';
import { Meter } from '../../../../../models/meter';
import { ActivatedRoute, Router } from '@angular/router';
import { MeterService } from '../../../../../service/meter.service';
import { Global } from '../../../../../service/global';
import { ErrorCatcher } from '../../../../../models/errorCatcher';
import { ErrorCatcherService } from '../../../../../service/errorCatcher.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-get-medidor',
  templateUrl: './getmedidor.component.html',
  styleUrls: ['./getmedidor.component.scss'],
  providers: [MeterService, ErrorCatcherService]

})
export class GetMedidorComponent implements OnInit, DoCheck {
    public title: string;
    public descripcion: string;
    public url: string;
    public meter: Meter;
    public loading: boolean;
    public medidores: Meter [] = [];
                        //   7  ^  7
    /**Registro de Errores Capturados @type ErrorCatcher */
    public errorCatched: ErrorCatcher;

    constructor(
      private location: Location,
      private route: ActivatedRoute,
      private router: Router,
      private meterService: MeterService,
      /**Servicio de Catpura de Errores */
      private errorCatcherService: ErrorCatcherService,

    ) {
        this.title = 'Cargando....';
        this.descripcion = 'Cargando....';
        this.url = Global.url + 'meter/';
        this.loading = true;
    }

    return() {
      this.location.back();
    }
    ngOnInit() {

      this.getMedidores();
    }

    ngDoCheck() {

    }
    // ------------------------------------------------------------------- //
    // Funcion de obtener Medidores en General
    // ------------------------------------------------------------------- //
    getMedidores() {
      this.meterService.showMeters().subscribe(
        response => {
          if (response.Medidores.length>=1) {
            this.title = 'Listado de Medidores';
            this.medidores = response.Medidores;
            this.loading = false;

          } else {
            this.loading = false;
            this.descripcion = 'No existen registros de medidores en la base de datos....';
            this.title = 'Aun no se han registrado medidores';
          }
        }, error => {
          console.log( error as any);
          this.errorCatched.description =  error.toString() as any;
          this.errorCatched.table = 'Meter';
          this.errorCatched.action = 'showMeter';
          this.errorCatched.title = 'Error al mostrar los Medidores';
          this.errorCatched.zone = 'show-meter';
          this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
          this.errorCatcherService.saveError(this.errorCatched).subscribe(
              response => {
                  alert(response.Message);
              });
        }
      );
    }

    busqueda(termino: string, busqueda: string) {

    }

    loadMeter(meter: Meter) {
      this.meter = meter;
      console.log(this.meter);

    }

    saveMeter() {

    }

}
