import { Component, OnChanges } from '@angular/core';
import { BillService } from '../../../../../service/bill.service';
import { Bill } from '../../../../../models/bill';
import { ActivatedRoute, Router } from '@angular/router';
import { RegisterService } from '../../../../../service/register.service';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { RateService } from '../../../../../service/rate.service';
import { Rate } from '../../../../../models/rate';
import { ImporteService } from '../../../../../service/importe.service';
import { Location } from '@angular/common';
import { DetalleFacturaService } from '../../../../../service/detalleFactura.service';


@Component({
  selector: 'app-add-factura',
  templateUrl: './add-factura.component.html',
  styleUrls: ['./add-factura.component.scss'],
  providers: [BillService, RegisterService, UserService, RateService, ImporteService, DetalleFacturaService]
})

export class AddFacturaComponent implements OnChanges {
  public title: string;
  public usuario: User;
  public bill: Bill;
  public loading = true;
  public registro;
  public tarifa: Rate;
  public detalles: any[];
  public importes;
  public descripcion: any[];
  public fechaActual = new Date;
  public registrada: boolean;
  public noRegister: boolean;

  constructor(
    private location: Location,
    private route: ActivatedRoute,
    private billService: BillService,
    private registerService: RegisterService,
    private userService: UserService,
    private rateService: RateService,
    private importeService: ImporteService,
    private detalleService: DetalleFacturaService,
    private router: Router
  ) {
    this.title = 'Cargando....';
    this.loading = true;
    this.initRegister();
    this.initBill();
    this.detalles = [];
    this.uniqueBill();
  }
  ngOnChanges() {

  }
  uniqueBill() {
    this.registrada = false;
    this.route.params.subscribe(
      params => {
        const registro = params.id;
        this.billService.getAll().subscribe(
          response => {
            for (const factura of response.Bill) {
              if (factura.registro === registro) {
                alert('factura ya ingresada');
                this.registrada = true;
                this.bill = factura;
                if (factura._id) {
                  this.detalleService.getDetalle(factura._id).subscribe(
                    responseDetalle => {
                      for (const importe of responseDetalle.Detalles) {
                        this.detalles.push(importe.importe);
                      }
                      this.detalles.sort((a, b) => {
                        if (a.descuento > b.descuento) {
                          return 1;
                        }
                        if (a.descuento < b.descuento) {
                          return -1;
                        }
                        return 0;
                      });
                      this.getTotal();
                    }, error => {
                      console.error(error as any);
                    }
                  );
                }
              }
            }
          }, error => {

          }
        );
      }
    );
  }

  initImportes() {
    this.importeService.getImportes().subscribe(
      response => {
        this.importes = response.Importes;
      }, error => {
        console.log(error as any);
      }
    );
  }
  initBill() {
    this.bill = {
      _id: null,
      numero: null,
      fecha: null,
      total: null,
      pago: null,
      tarifa: null,
      medidor: null,
      usuario: null,
      registro: null
    };
  }
  return() {
    this.location.back();
  }
  initRegister() {
    this.title = 'Facturar Registro del consumo del medidor';
    this.title = 'Registro de la Factura en base al consumo del medidor';
    this.route.params.subscribe(
      params => {
        const id = params.id;
        this.registerService.getOne(id).subscribe(
          response => {
            this.registro = response.Register;
            this.descripcion = [
              {
                nombre: 'Lectura: ' + this.registro.lectura + '. Consumo: ' + this.registro.consumo,
                detalle: 'Tarifa Base',
                valor: '$' + this.registro.base.toFixed(2),
                subtotal: '$' + this.registro.base.toFixed(2)
              },
              {
                nombre: 'Exceso: ' + this.registro.excessConsumo,
                detalle: 'Costo por Metro: $' + this.registro.excessM3.toFixed(2),
                valor: '$' + this.registro.excessCosto.toFixed(2),
                subtotal: '$' + this.registro.subtotal.toFixed(2)
              }
            ];
            this.initUsuario(this.registro.meter.user);
            this.initRate(this.registro.meter.rate);
            this.initImportes();
            this.getTotal();
          }, error => {
            console.error(error as any);
            if (error.status === 404) {
              alert('No se encuentra el registro de consumo!');
              this.noRegister = true;
            }
            this.loading = false;
          }
        );
      }
    );

  }

  date(year, monthIndex): Date {
    let newDate: Date;
    newDate = new Date(year, monthIndex);
    return newDate;
  }

  initRate(id) {
    this.rateService.findRate(id).subscribe(
      response => {
        this.tarifa = response.Rate;
        this.loading = false;
      }, error => {
        console.error(error as any);
        this.loading = false;
      }
    );
  }

  initUsuario(id) {
    this.userService.getUser(id).subscribe(
      response => {
        this.usuario = response.user;
        this.fechaActual.getDate();
      }, error => {
        console.error(error as any);
      }
    );
  }

  onSubmit() {
    if (this.bill.pago === null) {
      this.bill.pago = 'debe';
    }
    this.bill.tarifa = this.tarifa._id;
    this.bill.registro = this.registro._id;
    this.bill.usuario = this.registro.meter.user;
    this.bill.fecha = this.fechaActual.toString();
    this.bill.medidor = this.registro.meter._id;
    console.log(this.bill);
    this.billService.save(this.bill).subscribe(
      response => {
        console.log(response);
        if (this.detalles.length >= 1) {
          for (const importe of this.detalles) {
            const detalle = {
              detalle: 'detalle',
              factura: response.Factura._id,
              importe: importe._id
            };
            this.detalleService.save(detalle).subscribe(
              responseDetalle => {
                console.log(responseDetalle);
              }, error => {
                console.log(error as any);
              }
            );
          }
        }
        // for (const detalle of this.detalles) {
        // }
        alert(response.Message);
        setTimeout(() => {
          this.uniqueBill();
        }, 130);

      }, error => {
        console.error(error as any);
        if ((error.status as any) === 304) {
          alert('La factura ingresada ya existe');
        } else {
          alert('Error al ingresar la factura');
        }
      }
    );
  }
  getTotal() {
    this.bill.total = this.registro.subtotal;
    this.bill.total = parseFloat(this.bill.total.toFixed(2));
    for (const detalle of this.detalles) {
      if (detalle.descuento === true && detalle.percent === false) {
        this.bill.total = this.bill.total - parseFloat(detalle.costo.toFixed(2));
        detalle.total = this.bill.total.toFixed(2);
      } else if (detalle.descuento === false && detalle.percent === false) {
        this.bill.total = this.bill.total + parseFloat(detalle.costo.toFixed(2));
        detalle.total = this.bill.total.toFixed(2);
      } else if (detalle.descuento === true && detalle.percent === true) {
        detalle.subtotal = parseFloat(((detalle.costo / 100) * this.bill.total).toFixed(2));
        detalle.total = parseFloat((this.bill.total - detalle.subtotal).toFixed(2));
        this.bill.total = detalle.total;
      } else if (detalle.descuento === false && detalle.percent === true) {
        detalle.subtotal = ((parseFloat(detalle.costo.toFixed(2)) / 100) * this.bill.total);
        this.bill.total = this.bill.total + detalle.subtotal;
        detalle.subtotal = detalle.subtotal.toFixed(2);
        detalle.total = this.bill.total.toFixed(2);
      }
    }
    this.bill.total = parseFloat(this.bill.total.toFixed(2));

  }
  onDeleteImport(id) {
    for (let i = 0; i < this.detalles.length; i++) {
      const detalle = this.detalles[i];
      if (detalle._id === id) {
        this.importes.push(detalle);
        this.detalles.splice(i, 1);
        this.getTotal();
      }
    }
  }

  onAddImporte(id) {
    for (let i = 0; i < this.importes.length; i++) {
      const importe = this.importes[i];
      if (importe._id === id) {
        this.detalles.push(importe);
        this.detalles.sort((a, b) => {
          if (a.descuento > b.descuento) {
            return 1;
          }
          if (a.descuento < b.descuento) {
            return -1;
          }
          return 0;
        });
        this.importes.splice(i, 1);
        this.getTotal();
      }
    }
  }

  onPay() {
    this.billService.pay(this.bill).subscribe(
      response => {
        console.log(this.bill);
        localStorage.setItem('factura', this.bill._id);
        alert(response.Message);
        this.router.navigate(['/factura']);
      }, error => {
        console.error(error as any);
      }
    );
    console.log('pagando factura');
  }

}
