import { Component } from '@angular/core';
import { Bill } from '../models/bill';
import { BillService } from '../service/bill.service';
import { DetalleFacturaService } from '../service/detalleFactura.service';
import { Router } from '@angular/router';
import { User } from '../models/user';
import { UserService } from '../service/user.service';
import { MeterService } from '../service/meter.service';
import * as jsPDF from 'jspdf';

@Component({
  selector: 'app-imprimir-factura',
  templateUrl: './imprimirFactura.component.html',
  styleUrls: ['./imprimirFactura.component.scss'],
  providers: [BillService, DetalleFacturaService, MeterService]
})

export class ImprimirFacturaComponent {
  public bill: Bill;
  public loading: boolean;
  public descripcion: any[];
  public detalles: any[];
  public facturaId: string;
  public registro;
  constructor(
    private detalleService: DetalleFacturaService,
    private router: Router,
    private facturaService: BillService,
    private meterService: MeterService
  ) {
    this.facturaId = localStorage.getItem('factura');
    this.loading = true;
    this.detalles = [];
    this.initFactura(this.facturaId);
  }

  returnToStart() {
    localStorage.removeItem('factura');
    alert('No se encuentra la factura');
    this.router.navigate(['/inicio']);
  }
  initFactura(id) {
    this.facturaService.getFactura(id).subscribe(
      response => {
        if (response.factura) {
          this.bill = response.factura;
          console.log(response.Message);
          if (response.factura.registro) {
            this.registro = response.factura.registro;
            this.meterService.getMeter(this.registro.meter).subscribe(
              responseMeter => {
                this.registro.meter = responseMeter.meter;
              }, error => {
                console.error(error as any);
              }
            );
            this.descripcion = [
              {
                nombre: 'Lectura: ' + this.registro.lectura + '. Consumo: ' + this.registro.consumo,
                detalle: 'Tarifa Base',
                valor: '$' + this.registro.base.toFixed(2),
                subtotal: '$' + this.registro.base.toFixed(2)
              },
              {
                nombre: 'Exceso: ' + this.registro.excessConsumo,
                detalle: 'Costo por Metro: $' + this.registro.excessM3.toFixed(2),
                valor: '$' + this.registro.excessCosto.toFixed(2),
                subtotal: '$' + this.registro.subtotal.toFixed(2)
              }
            ];
            this.initDetalles(id);
          } else {
            this.returnToStart();
          }
        } else {
          this.returnToStart();
        }
      }, error => {
        console.error(error as any);
        this.returnToStart();
      }
    );

  }
  initDetalles(id) {
    this.detalleService.getDetalle(id).subscribe(
      responseDetalle => {
        for (const importe of responseDetalle.Detalles) {
          this.detalles.push(importe.importe);
        }
        this.detalles.sort((a, b) => {
          if (a.descuento > b.descuento) {
            return 1;
          }
          if (a.descuento < b.descuento) {
            return -1;
          }
          return 0;
        });
        this.getTotal();
      }, error => {
        console.error(error as any);
      }
    );
  }
  date(year, monthIndex): Date {
    let newDate: Date;
    newDate = new Date(year, monthIndex);
    return newDate;
  }

  getTotal() {
    this.bill.total = this.registro.subtotal;
    this.bill.total = parseFloat(this.bill.total.toFixed(2));
    for (const detalle of this.detalles) {
      if (detalle.descuento === true && detalle.percent === false) {
        this.bill.total = this.bill.total - parseFloat(detalle.costo.toFixed(2));
        detalle.total = this.bill.total.toFixed(2);
      } else if (detalle.descuento === false && detalle.percent === false) {
        this.bill.total = this.bill.total + parseFloat(detalle.costo.toFixed(2));
        detalle.total = this.bill.total.toFixed(2);
      } else if (detalle.descuento === true && detalle.percent === true) {
        detalle.subtotal = parseFloat(((detalle.costo / 100) * this.bill.total).toFixed(2));
        detalle.total = parseFloat((this.bill.total - detalle.subtotal).toFixed(2));
        this.bill.total = detalle.total;
      } else if (detalle.descuento === false && detalle.percent === true) {
        detalle.subtotal = ((parseFloat(detalle.costo.toFixed(2)) / 100) * this.bill.total);
        this.bill.total = this.bill.total + detalle.subtotal;
        detalle.subtotal = detalle.subtotal.toFixed(2);
        detalle.total = this.bill.total.toFixed(2);
      }
    }
    this.loading = false;
    this.bill.total = parseFloat(this.bill.total.toFixed(2));

  }

  imprimir() {
    window.print();
  }

  getPdf() {
    const id = document.getElementById('factura');
    const pdf = new jsPDF({
      format: 'A4'
    });
    pdf.fromHTML(id, 10, 6);
    pdf.save('Factura No. ' + this.bill.numero + '.pdf');
  }
}
