import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { User } from '../models/user';
import { Global } from './global';

@Injectable()
export class FollowService {
    public url: string;
    constructor(
        private _http : HttpClient

    ) {
        this.url = Global.url + 'follow/';
    }

    addFollow(token, follow): Observable<any> {
        let params = JSON.stringify(follow);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
        return this._http.post(this.url + 'savefollow', params, {headers:headers});
    }

    deleteFollow(token, id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
        return this._http.delete(this.url + 'deletefollow/' + id, {headers: headers});
    }

    getFollowing(token, id = null, page): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
        var url = this.url + 'following/';
        if (id != null) {
            url = this.url + 'following/' + id + '/' + page
        }
        return this._http.get(url, {headers: headers});
    }

    getFollowed(token, id = null, page): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
        var url = this.url + 'followed/';
        if (id != null) {
            url = this.url + 'followed/' + id + '/' + page
        }
        console.log('url: ' + url);

        return this._http.get(url, {headers: headers});
    }

    getMyFollows(token): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.get( this.url + 'follows/true', {headers: headers});
    }
// ********************************************************************************************************* //
}