import { Injectable } from "@angular/core";
import { Mora } from '../models/mora';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';


@Injectable()
export class MoraService {
    public url: string;
    public mora: Mora;

    constructor(
        private http: HttpClient
    ) {
        this.url = Global.url + 'mora/';
    }
    // Metodos------------------------------------------------------------------//
    // ------Post----------------------------------------------------------------//
    save(mora): Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const params = JSON.stringify(mora);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', token);
        return this.http.post(this.url + 'save', params, {headers});
    }
    // ------Get----------------------------------------------------------------//
    gets():Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', token);
        return this.http.get(this.url + 'show', {headers});
    }
    // ------Delete----------------------------------------------------------------//
    deleteMora(id): Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', token);
        return this.http.delete(this.url + 'delete/' + id, {headers})
    }
    // ------Put----------------------------------------------------------------//
    update(mora): Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const params = JSON.stringify(mora);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                         .set('Authorization', token);
        return this.http.put(this.url + 'update/'+mora._id, params, {headers});
    }
}
