import { Component, Input, DoCheck, OnChanges, OnInit } from '@angular/core';
import { RegisterService } from '../../../../../../service/register.service';
import { Register } from '../../../../../../models/register';
import { Router } from '@angular/router';


@Component({
    selector: 'app-show-registers',
    templateUrl: './showregisters.component.html',
    styleUrls: ['./showregisters.component.scss'],
    providers: [RegisterService]
})

export class ShowRegistersComponent implements OnChanges, OnInit {
    public title: string;
    public registers: Register[] = [];
    @Input() meterId;

    constructor(
        private _registerService: RegisterService,
        private router: Router
    ) {
        this.findRegisters();
        this.title = 'Listado de Registros';
    }
    ngOnInit() {
        this.findRegisters();

    }
    ngOnChanges() {
        this.findRegisters();
    }

    /**
     * Funcion de Busqueda de Registros
     */
    findRegisters() {
        if (this.meterId) {
            this._registerService.getRegisters(this.meterId).subscribe(
                response => {
                    if (response.registers) {
                        console.log(response.registers);

                        this.registers = response.registers;
                    } else { this.registers = null; }
                }, error => {
                    console.log( error as any);
                }
            );
        }
    }
    /**
     * Funcion para Devolver la Fecha
     */
    date(year, monthIndex): Date {
        let newDate: Date;
        newDate = new Date (year, monthIndex);
        return newDate;
    }

    verFactura(id){
      localStorage.setItem('factura', id);
      this.router.navigate(['/factura']);
    }
}
