import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';
import { UserService } from './user.service';
import { Register } from '../models/register';

@Injectable()
export class RegisterService {
    public url: string;
    constructor(
        private _http: HttpClient,
        private userService: UserService
    ) {
        this.url = Global.url + 'register/';
    }
    //-Funciones----------------------------------------------------------------//
    //--Metodos-----------------------------------------------------------------//
    //---Get--------------------------------------------------------------------//
    getRegisters(id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.userService.getToken());
        return this._http.get(this.url + 'show/' + id, { headers: headers });
    }

    getAllRegisters(): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.userService.getToken());
        return this._http.get(this.url + 'show-all', { headers: headers });
    }
    getOne(id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.userService.getToken());
        return this._http.get(this.url + 'get-one/'+id, { headers: headers });
    }
    //---Post-------------------------------------------------------------------//
    save(register: Register): Observable<any> {
        let params = JSON.stringify(register);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.userService.getToken());
        return this._http.post(this.url + 'save', params, { headers: headers });
    }
}
