import {Component, OnInit, Input} from '@angular/core';
import { UserService } from '../../../../../service/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../../service/global';
import { PublicationService } from '../../../../../service/publication.service';
import { Publication } from '../../../../../models/publication';

@Component({
    selector: 'app-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.css'],
    providers: [UserService, PublicationService]
})

export class TimeLineComponent implements OnInit {

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private userService: UserService,
        private publicationService: PublicationService

    ) {
        this.identity = this.userService.getIdentity();
        this.token = this.userService.getToken();
        this.url = Global.url + 'publication/';
        this.urlUser = Global.url + 'user/';
        this.title = 'Publicaciones del Usuario';
        this.page = 1;
    }
    public identity;
    public token;
    public title: string;
    public url: string;
    public urlUser: string;
    public status: string;
    public page;
    public publications: Publication[];
    public total;
    public pages;
    public itemsPerPage;

    @Input() user: string;
    public noMore = false;

    ngOnInit() {
        this.identity = this.userService.getIdentity();
        this.token = this.userService.getToken();
        console.log('usuario perfil: ' + this.user);
        if (this.identity == null) {
            console.log('time line iniciado...');
        } else {
            console.log('time line iniciado... usuario: ' + this.identity._id);
            this.getPublications(this.user, this.page);
        }

    }

    getPublications(user, page, adding = false) {

        this.publicationService.getPublicationUser(this.token, user, page).subscribe(
            response => {
                console.log(this.token, page);

                console.log('dentro de getpublicationUser' + response);
                if (response.publications) {
                    this.pages = response.total_pages;
                    this.total = response.total_items;
                    this.itemsPerPage = response.items_per_Page;
                    if (this.pages = 1) {
                        this.noMore = true;
                    }
                    if (this.total <= 0) { this.title = 'El usuario no tiene publicaciones que mostrar..'; }
                    else { this.title = 'Publicaciones del Usuario'; }
                    if (adding == false) {
                        this.publications = response.publications;
                    } else if (adding == true) {
                        let arrayA = this.publications;
                        let arrayB = response.publications;
                        this.publications = arrayA.concat(arrayB);
                        $('html, body').animate({ scrollTop: $('body').prop('scrollHeight') }, 500);
                    }
                    if (page > this.pages) {
                        console.log('exceso de paginas... depurar');
                        // this.router.navigate(['/home']);
                    }
                } else {
                    this.status = 'error';
                }
            }, error => {
                console.log('Error: ' +  error as any);

            }
        );
    }
    viewMore() {
        console.log('Actual: ' + this.publications.length + '. Total: ' + this.total + '. Objetos por Pagina: ' + this.itemsPerPage);

        if (this.publications.length >= (this.total - this.itemsPerPage)) {
            console.log(this.page);
            this.noMore = true;
            this.page = this.page + 1;
        } else if (this.publications.length == (this.total - this.itemsPerPage)) {
            this.noMore = true;
        } else {
            this.page = this.page + 1;
            console.log(this.page);
        }
        this.getPublications(this.user, this.page, true);
    }

    refresh(event) {
        this.getPublications(this.user, 1);
    }

}
