import { Component, OnInit } from "@angular/core";
import { ExtraService } from '../../../../../service/extra.service';
import { Extra } from '../../../../../models/extra';
import moment from 'moment';
import { Location } from '@angular/common';


@Component({
    selector: 'app-extras',
    templateUrl: './extras.component.html',
    styleUrls: ['./extras.component.scss'],
    providers: [ExtraService]
})

export class ExtrasComponent implements OnInit {
    public title: string;
    public description: string;
    public extra: Extra;
    public extras: Extra [] = [];
    public loading: boolean;
    public date: Date;
    public noChange: boolean;
    constructor(
        private location: Location,
        private extraService: ExtraService
    ) {
        this.title = 'Cargando...';
        this.description = 'Espere mientras se cargan los datos del servidor';
        this.loading = true;
        this.extra = {
            _id: null,
            rmuv: null,
            date: null,
            updated_at: null,
            updated_by: null
        }
    }
    return() {
      this.location.back();
    }
    
    ngOnInit() {
        this.loadExtras();
    }

    loadExtras() {
        this.extraService.getExtra().subscribe(
            response => {
                this.extras = response.extra;
                // this.extras.sort((a: any, b: any) =>
                // new Date(a.date).getTime() - new Date(b.date).getTime())
                this.loading = false;
                if (response.extra.length <=0) {
                    this.title = 'Aun no hay Registros de RMUV Ingresados....';
                    this.description = 'Por favor, para continuar Ingresar los registros de la "Remuneracion Mensual Unificada Vigente [R.M.U.V]"';
                } else {
                    this.description = 'Recuerde ingresar los datos de la "Remuneracion Mensual Unificade Vigente [R.M.U.V]" de forma periodica';
                    this.title = 'Registros de RMUV Ingresados....';
                }
            }
        );
    }

    /**Devuelve la Fecha del DatePicker
     * @param $event
     * selectedMonthAsText = moment().month(event.monthIndex).format('MMMM');
     */
    getDate(event: {year: number, monthIndex: number}) {
        // this.extra.date = event.year+'-'+event.monthIndex;
        this.extra.date  = new Date (event.year ,event.monthIndex);
        setTimeout(() => {
            for (const extra of this.extras) {
                const date1 = moment(new Date(extra.date));
                const date2 = moment(this.extra.date);
                console.log(date1+' === '+date2);
                const diferencia = date2.diff(date1, 'months');
                if (diferencia <= 0 ) {
                    this.noChange = true;
                    return alert('fecha ya ingresada');
                } else if (diferencia>=1) {
                    this.noChange = false;
                }
            }
        }, 350);
    }
    onSubmit() {
        console.log(this.extra);
        this.extraService.save(this.extra).subscribe(
            response => {
                console.log(response);
                alert(response.Message);
            }, error => {
            }
        );
    }
}

