import { Component, OnInit } from "@angular/core";
import { MoraService } from '../../../../../service/mora.service';
import { Mora } from '../../../../../models/mora';
import { Location } from '@angular/common';


@Component({
    selector: 'app-impuesto-mora',
    templateUrl: './impuesto-mora.component.html',
    styleUrls: ['./impuesto-mora.component.scss'],
    providers: [MoraService]
})
export class ImpuestoMoraComponent implements OnInit {
    public loading: boolean;
    public mora: Mora;
    public previousMoras: Mora[]=[];
    public title: string;
    public description: string;
    public editing: boolean;
    public example;
    public unique: boolean;
    public noMore: boolean;
    public changes: boolean;
    constructor (
        private moraService: MoraService,
        private location: Location
    ) {
        this.loading = true;
        this.title = 'Cargando...';
        this.description = 'Cargando...';
        this.unique = true;
        this.changes = false;
    }
    initMora() {
        this.mora = {
            _id: null,
            cantidad_meses: null,
            porcentaje: null,
            corte: null,
            edited_at: null,
            edited_by: null
        }
    }

    initPreviousMoras() {
        this.moraService.gets().subscribe(
            response => {
                this.previousMoras = response.Mora;
                this.previousMoras.sort((a,b) => (a.cantidad_meses - b.cantidad_meses));
                for (const previousMora of this.previousMoras) {
                    if (previousMora.corte === true) {
                        this.noMore = true;
                        alert('Los importes por mora ingresados ya han registrado un valor por corte. Si desea agregar mas por favor, elimine o edite uno de los valores actuales');
                    }
                }
                console.log(this.previousMoras);
                
            }, error => {
                console.error(error as any);
            }
        )
        this.changes = false;
    }
    return() {
        this.location.back();
      }
    ngOnInit() {
        this.title = 'Ingreso y control de los impuestos agregados por mora.';
        this.description = 'Recuerde actualizar esta lista con los valores de Impuestos Bancarios';
        this.loading = false;
        this.initMora();
        this.initPreviousMoras();
        this.editing = false;
    
    }

    uniqueMonth(mes) {        
        let isDuplicated: boolean;
        console.log('comprobando');
        
        for (const previousMora of this.previousMoras) {
            console.log(previousMora.cantidad_meses + ' == ' + mes);
            
            if (mes == previousMora.cantidad_meses) {
                console.log('encontro coincidencias');
                
                if (this.editing === true){
                    if (previousMora._id !== this.mora._id) {
                        isDuplicated = true;
                    }
                } else if (!this.editing || this.editing === false) {
                    isDuplicated = true;
                }
            }
        }

        if (isDuplicated == true) {
            alert('Este mes ya se ingreso');
            this.unique = false;
        } else {
            this.unique = true;
        }

    }
    save() {
        if (this.editing === false) {
            console.log('guardando nuevo');            
            this.moraService.save(this.mora).subscribe(
                response => {
                    console.info(response);
                    alert(response.Message);
                    this.changes=true;
                    this.initPreviousMoras();
                }, error => {
                    console.error(error as any);
                    alert('Error al Ingresar el Impuesto por Mora, ese mes ya se Ingreso.')
                }
            )
        } else if (this.editing === true) {
            console.log('guardando cambios');
            
            this.moraService.update(this.mora).subscribe(
                response => {
                    alert(response.Message);
                    this.changes = true;
                    this.initPreviousMoras();
                }, error => {
                    console.error(error as any);
                    alert('Error al Ingresar el Impuesto por Mora, ese mes ya se Ingreso.')
                }
            )

        }
    }

    onSubmit() {
        this.save();
    }

    onClear() {
        this.initMora();
        this.editing = false;
        this.initPreviousMoras();
    }

    onEdit(event) {
        this.editing = true;
        event = JSON.parse(event);
        this.mora = event;
        this.noMore = false;
    }
    onExample(){
        if (this.mora.porcentaje) {
            let porcentajes = this.mora.porcentaje / 100;
            let subtotal = 6;
            let subtotalMora = porcentajes * subtotal;
            let total = subtotal + subtotalMora;
            console.log('porecentaje total: ' + porcentajes.toFixed(2) + '. Subtotal: ' + subtotalMora.toFixed(2) + '. Mora: ' + subtotalMora.toFixed(2) + '. Total: ' + total.toFixed(2));
            this.example ='Subtotal: $' + subtotal.toFixed(2) + '. Impuesto por Mora: $' + subtotalMora.toFixed(2) + '. Total: $' + total.toFixed(2);
        }
    }

    noLimit(event){
        this.initPreviousMoras();
        if (event==true) {
            this.noMore = false;
        }
    }
}