// Componentes Basicos del Modulo
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

// importar componente de Moment
import { MomentModule } from 'angular2-moment';

// Rutas
import { MessagesRoutingModule } from './messages.routing';

// Componentes de la aplicacion
import { MainComponent } from './components/main/main.component';
import { AddComponent } from './components/main/add/add.component';
import { ReceivedComponent } from './components/main/received/received.component';
import { SendedComponent } from './components/main/sended/sended.component';
import { UserService } from '../service/user.service';
import { UserGuard } from '../service/user.guard';

// Carga de Componentes en el modulo
@NgModule({
    declarations: [
        MainComponent,
        AddComponent,
        ReceivedComponent,
        SendedComponent
    ],
    imports: [
        CommonModule,
        FormsModule,
        MessagesRoutingModule,
        MomentModule
    ],
    exports: [
        MainComponent,
        AddComponent,
        ReceivedComponent,
        SendedComponent
    ],
    providers: [
        UserService,
        UserGuard
    ],
    bootstrap: [MainComponent]
})
export class MessagesModule { }
