// !!! modulos Base de Angular
import { AppComponent } from './app.component';
import { appRoutingProviders, routing } from './app.routing';
import { BrowserModule } from '@angular/platform-browser';
import { LOCALE_ID, NgModule } from '@angular/core';
// !!! modulos Base de Angular

// >> Componentes Internos
  // >>> Formularios
import { FormsModule } from '@angular/forms';
  // <<< Formularios

  // >>> Http [conexion con servidor y caracteristicas de comunicacion]
import { HttpClientModule } from '@angular/common/http';
  // <<< Http

  // >>> Moment [Pipes para dar estilo y transformar tipos de fecha y hora]
import { MomentModule } from 'angular2-moment';
  // <<< Moment

  // >>> Paginacion
import { NgxPaginationModule } from 'ngx-pagination';
// <<< Paginacion
import { NgxMaskModule } from 'ngx-mask';
// << Componentes Internos

// >> Configuracion Global y Local
  // >>> Importar el modulo para registrar la informacion de region (idioma) //
import { registerLocaleData, DatePipe } from '@angular/common';
  // <<< Importar el modulo para registrar la informacion de region (idioma) //

  // >>> Importar locales con los strings de idiomas
import localeEsEc from '@angular/common/locales/es-EC';
import localePy from '@angular/common/locales/es-PY';
import localePt from '@angular/common/locales/pt';
import localeEn from '@angular/common/locales/en';
  // <<< Importar Locales

    // >>>> registrar los locales que se pudieran utilizar con el nombre a la hora de proveer la Region
registerLocaleData(localeEsEc, 'es-Ec');
registerLocaleData(localePy, 'es');
registerLocaleData(localePt, 'pt');
registerLocaleData(localeEn, 'en');
    // <<<< registrar los locales que se pudieran utilizar con el nombre a la hora de proveer la Region

// << Configuracion Global y Local


// >>Componentes, modulos y servicios creados
  // >>>Modulos
import { MessagesModule } from './messages/messages.module';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
  // <<<Modulos

  // >>>Servicios
import { UserService } from './service/user.service';
import { UserGuard } from './service/user.guard';
  // <<<Servicios

  // >>>Componentes

  // <<<Componentes
  // >>Componente de Pruebas
import { TestComponent } from './test/test.component';
import { AppClientModule } from './client/client.module';
import { LoginComponent } from './logins/components/login/login.component';
import { SharedModule } from './shared.module';
import { ImprimirFacturaComponent } from './ImprimirFactura/ImprimirFactura.component';
import { UnauthorizedComponent } from './client/components/unauthorized/unauthorized.component';
import { FechasPipe } from './pipes/fechas.pipe';
  // <<Componente de Pruebas
// <<Componentes, modulos y servicios creados


// Hash Location

// import { HashLocationStrategy, LocationStrategy } from '@angular/common';

@NgModule(
  {
    declarations: [
    AppComponent,
    TestComponent,
    LoginComponent,
    ImprimirFacturaComponent,
    UnauthorizedComponent,
    FechasPipe
  ],
  imports: [
    BrowserModule,
    routing,
    FormsModule,
    HttpClientModule,
    MomentModule,
    MessagesModule,
    NgxPaginationModule,
    BrowserAnimationsModule,
    AppClientModule,
    SharedModule,
    NgxMaskModule.forRoot()
  ],
  providers: [
    appRoutingProviders,
    { provide: LOCALE_ID, useValue: 'es-Ec' },
    UserService,
    UserGuard,
    DatePipe
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
