import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../../../../service/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../service/global';
import { PublicationService } from '../../../../service/publication.service';
import { Publication } from '../../../../models/publication';

@Component({
    selector: 'app-publication',
    templateUrl: './publication-lte.component.html',
    styleUrls: ['./publication-lte.component.scss'],
    providers: [UserService, PublicationService]
})

export class PublicationComponent implements OnInit {

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private userService: UserService,
        private publicationService: PublicationService

    ) {
        this.identity = this.userService.getIdentity();
        this.token = this.userService.getToken();
        this.url = Global.url + 'publication/';
        this.urlUser = Global.url + 'user/';
        this.title = 'TimeLines - Noticias';
        this.page = 1;
    }
    public identity;
    public token;
    public id;
    public title: string;
    public url: string;
    public urlUser: string;
    public status: string;
    public page;
    public publication: Publication;
    public publications: Publication[];
    public total;
    public pages;
    public itemsPerPage;

    @Input() user: string;
    public noMore = false;

    ngOnInit() {
        this.identity = this.userService.getIdentity();
        this.token = this.userService.getToken();
        if (this.identity == null) {
        } else {
            this.id = this.identity._id;
            this.getPublications(this.page);
        }

    }

    getPublications(page, adding = false) {
        this.publicationService.getPublication(this.token, page).subscribe(
            response => {
                if (response.publications) {
                    this.pages = response.total_pages;
                    this.total = response.total_items;
                    this.itemsPerPage = response.items_per_Page;
                    if (this.total <= 0) { this.title = 'TimeLines - Noticias: No hay publicaciones que mostrar..';
                    } else { this.title = 'TimeLines - Noticias'; }
                    if (adding == false) {
                        this.publications = response.publications;
                    } else if (adding == true) {
                        var arrayA = this.publications;
                        var arrayB = response.publications;
                        this.publications = arrayA.concat(arrayB);
                        $("html, body").animate({ scrollTop: $('body').prop("scrollHeight") }, 500);
                    }
                    if (page > this.pages) {
                        this.router.navigate(['/inicio']);
                    }
                } else {
                    this.status = 'error';
                }
            }, error => {
                console.log('Error: ' + <any>error);

            }
        );
    }
    viewMore() {
        console.log('Actual: ' + this.publications.length + '. Total: ' + this.total + '. Objetos por Pagina: ' + this.itemsPerPage);

        if (this.publications.length >= (this.total - this.itemsPerPage)) {
            console.log(this.page);
            this.noMore = true;
            this.page = this.page + 1;
        } else if (this.publications.length == (this.total - this.itemsPerPage)) {
            this.noMore = true;
        } else {
            this.page = this.page + 1;
            console.log(this.page);
        }
        this.getPublications(this.page, true);
    }

    refresh(event = null) {
        this.getPublications(1);
    }

    deletePublication() {

        this.publicationService.deletePublication(this.token,  this.publication._id).subscribe(
            response => {
                this.refresh();
                console.log('publicacion borrada');
            }, error => {
                console.log(<any>error);

            }
        );
    }


    getPub(publication){
        console.log('publicacion: ' );
        console.log(publication);

        this.publication = publication;
    }
    clearPub(){
        setTimeout(()=>{
            this.publication = null
        }, 1500);
    }
}
