import { Component, OnInit, EventEmitter, Output  } from '@angular/core';
import { UserService } from '../../../../../service/user.service';
import { MessageService } from '../../../../../service/messages.service';
import { Global } from '../../../../../service/global';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    selector: 'app-message-list',
    templateUrl: './message-list.component.html',
    styleUrls: ['./message-list.component.scss'],
    providers: [UserService, MessageService]
})

export class MessageListComponent implements OnInit {
    public title: string;
    public messages: [];
    public identity;
    public token;
    public url: string;
    public status: string;
    public more: boolean;
    public urlUser: string;

    // Evento de componente hijo a trasnmitir en el componente padre
    @Output() total = new EventEmitter<string>();

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private messageService: MessageService,
        private userService: UserService
    ) {
        this.title = 'Mensajes Recibidos';
        this.identity = userService.getIdentity();
        this.token = userService.getToken();
        this.url = Global.url + 'message/';
        this.urlUser = Global.url + 'user/';

    }
    ngOnInit() {
        this.getShortMessages();
    }
    getShortMessages() {
        this.messageService.getMyMessage(this.token, 1).subscribe(
            response => {
                if (response.Pages >= 5) { this.more = true; }
                this.total.emit(response.Total + '.' + response.Messages[0].created_at);
                this.messages = response.Messages;
            }, error => {
                console.log( error as any);
            }
        );
    }
}
