import { Component, OnInit, DoCheck, OnChanges } from '@angular/core';
import { Router, ActivatedRoute} from '@angular/router';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { UploadService } from '../../../../../service/upload.service';
import { Global } from '../../../../../service/global';
import { ErrorCatcher } from '../../../../../models/errorCatcher';
import { ErrorCatcherService } from '../../../../../service/errorCatcher.service';
@Component ({
    selector: 'app-user-edit',
    templateUrl: './user-edit.component.html',
    styleUrls: ['./user-edit.component.css'],
    providers: [UserService, UploadService, ErrorCatcherService]
})

export class UserEditComponent implements OnInit, OnChanges {
    public title: string;
    public user: User;
    public identity: User;
    public token: string;
    public status: string;
    public url: string;
    /**
     * Registro de Errores Capturados @type ErrorCatcher
     */
    public errorCatched: ErrorCatcher;
    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private userService: UserService,
        private uploadService: UploadService,
        /**
         * Servicio de Catpura de Errores
         */
        private errorCatcherService: ErrorCatcherService,
    ) {
        this.title = 'Actualizar Mis Datos...';
        this.user = this.userService.getIdentity();
        this.identity = this.user;
        this.token = this.userService.getToken();
        this.url = Global.url + 'user/';

    }

    ngOnInit() {
        this.onLoged();
    }
    ngOnChanges(){
        this.onLoged();
    }
    onSubmit () {
        this.userService.updateUser(this.user).subscribe(
            response => {
                if (!response.user) {
                    this.status = 'error';
                } else {
                    this.status = 'success';
                    // localStorage.setItem('identity', JSON.stringify(this.user));
                    this.uploadService.makeFileRequest(this.url + 'image/' + this.user._id, [], this.filesToUpload, this.token, 'image')
                        .then((result: any) => {
                            console.log('dentro de makeFileRequest');
                            this.user.image = result.user.image;
                            this.identity = this.user;
                            localStorage.setItem('identity', JSON.stringify(this.identity));
                        }).catch((result: any) => {
                            console.log('error' + result);
                        });
                }
            },
            error => {
                console.log(<any>error);
                this.errorCatched.description = <any>error.toString();
                this.errorCatched.table = 'User'
                this.errorCatched.action = 'editUser';
                this.errorCatched.title = 'Error al editar el usuario';
                this.errorCatched.zone = 'user-edit'
                this.errorCatched.code = this.errorCatched.zone +'-' + this.errorCatched.action + '-' + this.errorCatched.table;
                this.errorCatcherService.saveError(this.errorCatched).subscribe(
                    response => {
                        alert(response.Message)
                    });
            }
        );
    }
    onLoged(){
        if ( localStorage.getItem('identity') == null || localStorage.getItem('identity') == 'undefined' ) {
            console.log('not loged yet');
            this.router.navigate(['/home']);
        } else if (  localStorage.getItem('identity') != null || localStorage.getItem('identity') != 'undefined'  ) {
            console.log('loged');
        }
    }
    public filesToUpload: Array<File>;
    fileChangeEvent(fileInput: any){
        this.filesToUpload = <Array<File>>fileInput.target.files;
        console.log(this.filesToUpload);

    }
}
