import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, ɵEmptyOutletComponent } from '@angular/router';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { TablaService } from '../../../../../service/tabla.service';
import { Tabla } from '../../../../../models/tabla';
import { Permiso } from '../../../../../models/permiso';
import { Location } from '@angular/common';


@Component({
  selector: 'app-register-employees',
  templateUrl: './register-employees.component.html',
  styleUrls: ['./register-employees.component.scss'],
  providers: [UserService, TablaService, ]
})
export class RegisterEmployeesComponent implements OnInit {
  public identity;
  public title: string;
  public user: User;
  public status: boolean;
  public cedulaWarning: boolean;
  public onEdit: boolean;
  public employees: boolean;
  public tablas: Tabla[] = [];
  public permisos: Permiso [] = [];
  public ingresado: boolean;
  public cedulaCorrecta: boolean;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private tableService: TablaService,
    private location: Location
  ) {
    this.title = 'Registro de Empleados';
    this.employees = true;
    this.cedulaWarning = false;
    this.identity = JSON.parse(localStorage.getItem('identity'));
    this.user = new User(
      '',    // "_id"
      '',    // "cuenta"
      '',    // "contrase"
      '',    // "correo"
      '',    // "nombre"
      '',    // "image"
      '',    // "apellido"
      '',    // "cedula"
      '',    // "direccion"
      '',    // "telefono"
      '',    // "fecha_nacimiento"
      '',    // "sexo"
      '',    // "role_user"
      true,  // "activa"
      ''
    );
  }
  return() {
    this.location.back();
  }
  ngOnInit() {
    console.log('componente de Registro cargado...');
    // this.loadTables();
  }
  verifyEditCedula() {
    if (this.cedulaWarning === true) {
      let Message = 'Advertnecia, Modificar la cedula podria ocacionar fallos en el sistema, ';
      Message = Message + 'se recomienda hacerlo solo en casos excepcionales. ';
      Message = Message + 'Queda bajo su responsabilidad los daños ocacionados por esta accion';
      const response = confirm(Message);
      if (response === true) {
        this.cedulaWarning = false;
      } else {
        this.cedulaWarning = true;
      }
    }
  }
  editarUser(user): void {
    user = JSON.parse(user);
    this.user = user;
    this.ingresado = true;
    console.log(this.user);
    // this.user.contrase = '';
    this.onEdit = true;
    this.cedulaWarning = true;
    this.status = null;
    // this.EditicionDeUsuario.emit(user);
  }
  save(form) {
    this.userService.register(this.user).subscribe(
      response => {
          if (response.User && response.User._id) {
              this.status = true;
              form.reset();
              alert(response.Message);
              // this.ingresado = true;
              // this.user = response.User;

          } else {
              alert('No se pudo ingresar este usuario');
              this.status = false;
          }
      }, error => {
          console.log(error.error.Message);
          alert(error.error.Message);
          this.status = false;
          console.log( error as any);
      });
  }
  update(form) {
    this.userService.updateCommons(this.user).subscribe(
      response => {
        if (response.User && response.User._id) {
          this.status = true;
          alert(response.Message);
          this.user = response.User;
          this.onEdit = null;
        } else {
          alert(response.Message);
        }
      }, error => {
        console.log(error as any);
        alert(error.Message);
      });
  }
  onSubmit(form) {
    console.log(this.user);
    if (this.onEdit) {
      this.update(form);
    } else if (!this.onEdit) {
      this.save(form);
    }
    // this.userService.register(this.user).subscribe(
    //   response => {
    //     if (response.User && response.user._id) {
    //       this.status = true;
    //       alert('El Usuario: ' + response.User.nombre + ' ' + response.User.apellido + ', ha sido Ingresado');
    //     } else {
    //       alert('El Usuario: ' + response.User.nombre + ' ' + response.User.apellido + ', ya Existe');
    //       this.status = false;
    //     }
    //   },
    //   error => {
    //     alert('El Usuario no se ha podido Ingresar');
    //     this.status = false;
    //     console.log( error as any);
    //   }
    // );
  }
  loadTables( ) {
    this.tableService.getTables().subscribe(
      response => {
        this.tablas = response.Tabla;
        this.permisos = [], [];
        // tslint:disable-next-line: prefer-for-of
        for (let index = 0; index < this.tablas.length; index++) {
          const element = this.tablas[index];
          this.permisos.push({
            _id : null,
            tabla: element._id,
            crear: false,
            leer: false,
            actualizar: false,
            borrar: false,
            empleado: null
          });
        }
      }, error => {
        console.log(error as any);
      });
  }
  ingresar() {
    console.log(this.permisos);
    // for (let index = 0; index < this.permisos.length; index++) {
    //   const element = this.permisos[index];
    //   // this.permisoService
    // }
  }


  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length === 10) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i =  i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.cedulaCorrecta = true;
        } else {
          this.cedulaCorrecta = false;
        }
      }
    }
  }

  reset(form) {
    form.reset();
    this.cedulaWarning = null;
  }


}
