import { Component, OnInit, DoCheck } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UserService } from '../../../service/user.service';
import { User } from '../../../models/user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  providers: [UserService]
})
export class LoginComponent implements OnInit, DoCheck {
  public title: string;
  public user: User;
  public identity;
  public token;
  public status: boolean;
  public admin: boolean;
  public counter = 0;
  public cedulaCorrecta: boolean;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService
  ) {
    this.title = 'Ingresa tus datos para Iniciar sesion';
    this.admin = false;
    this.user = new User(
      '',         // "_id"
      '',         // "cuenta"
      '',         // "contrase"
      '',         // "correo"
      '',         // "nombre"
      '',         // "image"
      '',         // "apellido"
      '',         // "cedula"
      '',         // "direccion"
      '',         // "telefono"
      '',         // "fecha_nacimiento"
      '',         // "sexo"
      '',         // "role_user"
      true,     // "activa"
      ''          // Token
    );
  }
  ngOnInit() {
    this.onLoged();
  }
  ngDoCheck() {
    // this.onLoged();
  }
  onLoged() {
    if (localStorage.getItem('identity') == null || localStorage.getItem('identity') === 'undefined') {
      console.log('not loged yet');
    } else if (localStorage.getItem('identity') != null || localStorage.getItem('identity') !== 'undefined') {
      this.router.navigate(['/']);
      console.log('loged');
    }
  }

  loginUser(user) {
    this.userService.login(user).subscribe(
      response => {
        this.identity = response.user;
        console.log('respuesta' + this.identity);

        if (this.identity && this.identity._id !== 'undefined') {
          // localStorage.setItem('role_user', this.identity.role_user);
          localStorage.setItem('identity', JSON.stringify(this.identity));
          console.log('respuesta' + this.identity);
          this.getToken();
          // alert('El Usuario: ' + response.user.nombre + ' ' + response.user.apellido + ', ha sido Ingresado');

        } else if (!this.identity && !this.identity._id) {
          alert('Error en la cuenta y/o contraseña');
          console.log('respuesta' + this.identity);
          this.status = false;
        }
      },
      error => {
        const errorMessage = error as any;
        console.log(errorMessage);
        alert('Error al loguear el usuario');
        console.log(error as any);
      }
    );
  }

  onSubmit() {
    // loguear al Usuario //
    console.log('on submit ejecutado..');

    if (this.admin === false) {
      this.loginUser(this.user);
    } else if (this.admin === true) {
      this.user.role_user = 'admin';
      this.userService.register(this.user).subscribe(
        response => {
          this.identity = response.User;
          localStorage.setItem('identity', JSON.stringify(response.User));
          this.getToken();
          this.router.navigate(['/inicio']);
        }, error => {
          console.error(error as any);
          alert('Datos Duplicados, revise su cedula, su correo o su nombre de cuenta...');
        }
      );
    }
  }
  getFollow() {
    this.userService.getUsers(1).subscribe(
      response => {
        console.log(response.follows);

      }
    );
  }
  getToken() {
    this.userService.login(this.user, 'true').subscribe(
      response => {
        this.token = response.token;
        if (!this.token) {
          alert('Error en el correo y/o contraseña');
        } else if (this.token) {
          localStorage.setItem('token', JSON.stringify(this.token));
          // this.getCounters();
          // alert('El Usuario: ' + response.user.nombre + ' ' + response.user.apellido + ', ha sido Ingresado');
        }
      },
      error => {
        const errorMessage = error as any;
        console.log(errorMessage);
        alert('Error al loguear el usuario');
        console.log(error as any);
      }
    );
  }
  getCounters() {
    this.userService.getCounters().subscribe(
      response => {
        console.log(response.counters);
        localStorage.setItem('stats', JSON.stringify(response.counters));
        this.router.navigate(['/']);
      },
      error => {
        console.log(error as any);
      }
    );
  }

  onAdmin() {
    this.counter++;
    console.log(this.counter);

    if (this.counter >= 6) {
      if (this.admin === false) {
        console.log('entrando en modo admin');
        this.counter = 0;
        this.title = 'bienvenido al modo administrador!, ingrese un usuario y una contraseña para crear una cuenta de Administrador Global';
        this.admin = true;
      } else if (this.admin === true) {
        console.log('Saliendo del modo administrador');
        this.counter = 0;
        this.title = 'Ingresa tus datos para Iniciar sesion';
        this.admin = false;
      }
    }
    setTimeout(() => {
      this.counter = 0;
      console.log(this.counter);
    }, 1000);
  }

  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length === 10) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i =  i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.cedulaCorrecta = true;
        } else {
          this.cedulaCorrecta = false;
        }
      }
    }
  }

}
