import { Component, OnInit, DoCheck } from "@angular/core";
import { Router, ActivatedRoute, Params } from '@angular/router';

// Servicios //
//-----Seguidores //
import { FollowService } from "../../../../service/follow.service";
//-----Mensajes //
import { MessageService } from "../../../../service/messages.service";
//-----Usuario //
import { UserService } from "../../../../service/user.service";
//-----Global //
import { Global } from "../../../../service/global";

// Modelos //
//-----Mensajes //
import { Message } from "../../../../models/message";

@Component({
    selector: 'sended',
    templateUrl: './sended.component.html',
    styleUrls: ['./sended.component.scss'],
    providers: [UserService, MessageService, FollowService]

})
export class SendedComponent implements OnInit, DoCheck {
    public title: string;
    public messages: Message[];
    public identity;
    public token;
    public url: string;
    public status: string;
    public page;
    public total;
    public pages;
    public nextPage;
    public prevPage;
    public urlUser: string;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private _messageService: MessageService,
        private _followService: FollowService,
        private userService: UserService
    ) {
        this.title = 'Mensajes Enviados...';
        this.identity = userService.getIdentity();
        this.token = userService.getToken();
        this.url = Global.url + 'message/';
        this.urlUser = Global.url + 'user/';
    }

    ngOnInit() {
        this.actualPage();
    }

    ngDoCheck() {

    }

    actualPage() {
        this.route.params.subscribe(params => {
            let page = +params['page'];
            this.page = page;
            if (!page || page == 1) this.page = 1, this.prevPage = null, this.nextPage = 2, console.log('pagina 1');
            else if (page && page >= 2) {
                this.nextPage = page + 1;
                this.prevPage = page - 1;
                if (this.prevPage <= 0) this.prevPage = 1;
            }
            // devolver listado de mensajes
            this.getMessages(this.page)
        })
    }

    getMessages(page) {
        if (page == null || page == 0) page = 1;
        this._messageService.getEmittMessage(this.token, page).subscribe(
            response => {
                this.pages = response.Pages;
                this.total = response.Total;
                this.messages = response.Messages;
                console.log('Datos: -Pages: ' + this.pages + '. -Total [object]: ' + this.total + '. -Page: ' + this.page);

                console.log(response);
            }, error => {
                console.log(<any>error);
            }
        );
    }
}
