import { Injectable } from "@angular/core";
import { Extra } from '../models/extra';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';


@Injectable()
export class ExtraService{
    public url: string;
    public extra: Extra;
    constructor(
        private _http: HttpClient
    ) {
        this.url = Global.url + 'extra/';
    }

    // Metodos------------------------------------------------------------------//
    //------Post----------------------------------------------------------------//
    save(extra): Observable<any>{
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(extra);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.post(this.url + 'save' , params, { headers: headers });
    }
    //------Get----------------------------------------------------------------//
    getExtra(): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.get(this.url + 'show', { headers: headers})
    }
    //------Put----------------------------------------------------------------//
    updateExtra(extra): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(extra);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.post(this.url + 'update/' + extra._id , params, { headers: headers });
    }
    //------Delete----------------------------------------------------------------//
    deleteExtra(id): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.delete(this.url + 'delete/' + id , { headers: headers });
    }
}