import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { DetalleFactura } from '../models/detalleFactura';
import { Observable } from 'rxjs';


@Injectable()

export class DetalleFacturaService {
  public url: string;
  public detalleFactura: DetalleFactura;
  constructor(
    private http: HttpClient
  ) {
    this.url = Global.url + 'detalle-factura/';
  }

  save(detalleFactura): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const params = JSON.stringify(detalleFactura);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.post(this.url + 'save', params, { headers });
  }

  getDetalle(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.get(this.url + 'detalle/' + id, { headers });

  }
}
