import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';
import { UserService } from './user.service';

@Injectable()
export class RateService {
    public url: string;

    constructor(
        private _http: HttpClient,
        private userService: UserService
    ) {
        this.url = Global.url + 'rate/';
    }
    // -Funciones----------------------------------------------------------------//
    // --Metodos-----------------------------------------------------------------//
    // ---Get--------------------------------------------------------------------//
    // ----Devolver Tarifas------------------------------------------------------//
    getRate(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.userService.getToken());
        return this._http.get(this.url + 'show', { headers });
    }
    findRate(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', this.userService.getToken());
        return this._http.get(this.url + 'get-one/'+id, { headers });
    }
    // ---Get--------------------------------------------------------------------//
    save(rate): Observable<any> {
        const params = JSON.stringify(rate);
        const token = JSON.parse(localStorage.getItem('token'));
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.post(this.url + 'save', params, {headers} );
    }
    // ---Put--------------------------------------------------------------------//
    update(rate): Observable<any> {
        const params = JSON.stringify(rate);
        const token = JSON.parse(localStorage.getItem('token'));
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this._http.put(this.url + 'update/' + rate._id, params, {headers} );
    }

}
