import { Component, OnInit } from "@angular/core";
import { RateService } from '../../../../../service/rate.service';
import { Rate } from '../../../../../models/rate';
import { stringify } from '@angular/compiler/src/util';
import { ErrorCatcher } from '../../../../../models/errorCatcher';
import { ErrorCatcherService } from '../../../../../service/errorCatcher.service';


@Component({
    selector: 'app-edit-rate',
    templateUrl: './editrate.component.html',
    styleUrls: ['./editrate.component.scss'],
    providers:[RateService, ErrorCatcherService]
})

export class EditRateComponent implements OnInit{
    public title: string;
    public rate: Rate;
    public status: string;
    /**Registro de Errores Capturados @type ErrorCatcher */
    public errorCatched: ErrorCatcher;

    constructor(
        private rateService: RateService,
        /**Servicio de Catpura de Errores */
        private errorCatcherService: ErrorCatcherService,
    ){
        this.title = 'Ingreso de Tarifas';
        this.initRate();
    }

    initRate() {
        this.rate = new Rate(
            '',
            '',
            0,
            true,
            ''
        );
    }
    ngOnInit(){

    }

    onSubmit(form){
        console.log(this.rate);
        console.log(form);

        this.rateService.update(this.rate).subscribe(
            response =>{
                console.log(response);
                alert(response.Message);
            },error => {
                console.log(<any>error);
                this.errorCatched.description = <any>error.toString();
                this.errorCatched.table = 'Rate'
                this.errorCatched.action = 'editRate';
                this.errorCatched.title = 'Error al editar la Tarifa';
                this.errorCatched.zone = 'edit-rate'
                this.errorCatched.code = this.errorCatched.zone +'-' + this.errorCatched.action + '-' + this.errorCatched.table;
                this.errorCatcherService.saveError(this.errorCatched).subscribe(
                    response => {
                        alert(response.Message)
                    });
            }
        )
    }
}
