import { Component, OnInit } from "@angular/core";
import { User } from '../../../../../models/user';
import { Location } from '@angular/common';



@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss']
})

export class UserListComponent implements OnInit {
  public title: string;
  public users: User[] = [];
  public SearchedUsers: string;
  constructor(
    private location: Location
  ) {
    this.title = 'Cargando....';
  }

  ngOnInit() {
    this.title = 'Lista de Usuarios de Medidores';
  }

  getData(event) {
    this.users = JSON.parse(event);
  }
  return() {
    this.location.back();
  }

  find(termino) {
    termino = termino.toLowerCase();
    let localSearch: User[] = [];
    localSearch = [], [];
    if (this.users) {
      for (const user of this.users) {
        let datos = user.nombre + user.apellido + user.direccion + user.cedula;
        if (user.cuenta) {
          datos = datos + user.cuenta;
        }
        datos = datos.toLowerCase().replace(/ /g, '');
        datos = datos.toLowerCase().replace(/-/g, '');
        if ((datos.indexOf(termino)) >= 0) {
          localSearch.push(user);
        }
      }
    }
    this.SearchedUsers = JSON.stringify(localSearch);
    console.log(localSearch);
  }
}
