import { Injectable } from "@angular/core";
import { Importe } from '../models/importe';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';


@Injectable()
export class ImporteService {
    public url: string;
    public importe: Importe;

    constructor(
        private http: HttpClient
    ) {
        this.url = Global.url + 'importe/';
    }
    // Metodos------------------------------------------------------------------//
    //------Post----------------------------------------------------------------//
    save(importe): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(importe);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
        .set('Authorization', token);
        return this.http.post(this.url + 'save' , params, { headers: headers });
    }
    //------Get----------------------------------------------------------------//
    getImportes(): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this.http.get(this.url + 'show', { headers: headers})
    }
    //------Put----------------------------------------------------------------//
    updateImporte(importe): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let params = JSON.stringify(importe);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this.http.put(this.url + 'update/' + importe._id , params, { headers: headers });
    }
    //------Delete----------------------------------------------------------------//
    deleteImporte(id): Observable<any> {
        let token = JSON.parse(localStorage.getItem('token'));
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this.http.delete(this.url + 'delete/' + id , { headers: headers });
    }
}