import { Injectable } from '@angular/core';
import { Bill } from '../models/bill';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';


@Injectable()

export class BillService {
  public url: string;
  public bill: Bill;
  constructor(
    private http: HttpClient
  ) {
    this.url = Global.url + 'bill/';
  }

  save(bill: Bill): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const params = JSON.stringify(bill);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.post(this.url + 'save', params, { headers });
  }
  getAll(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.get(this.url + 'get-all', { headers });
  }
  pay(bill): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const params = JSON.stringify(bill);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.put(this.url + 'pay/' + bill._id, params, { headers });
  }

  getFactura(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.get(this.url + 'show/' + id, { headers });

  }

}
