import { Component, OnInit, DoCheck, EventEmitter, Input, Output } from '@angular/core';
import { UserService } from '../../../../../service/user.service';
import { Global } from '../../../../../service/global';
import { Publication } from '../../../../../models/publication';
import { PublicationService } from '../../../../../service/publication.service';
import { UploadService } from '../../../../../service/upload.service';

@Component({
    selector: 'app-new-publication',
    templateUrl: './new-publication.component.html',
    styleUrls: ['./new-publication.component.scss'],
    providers: [UserService, PublicationService, UploadService]
})
export class NewPublicationComponent implements OnInit, DoCheck {
    public identity;
    public token;
    public stats;
    public url;
    public urlPublication;
    public status;
    public publication;
    public imagen: string;

    @Output() sended = new EventEmitter();

    constructor(
        private userService: UserService,
        private publicationService: PublicationService,
        private uploadService: UploadService
    ) {
        this.identity = this.userService.getIdentity();
        this.token = this.userService.getToken();
        this.stats = this.userService.getStats();
        this.url = Global.url + 'user/';
        this.urlPublication = Global.url + 'publication/';
        console.log(this.urlPublication);

        if (this.identity) {
            this.publication = new Publication("","","", "", "", "", "", "", "", this.identity._id);
        }
    }

    ngOnInit() {
        console.log('sidebar cargado correctamente . . . . ');
        this.refreshStats();
    }
    ngDoCheck() {
        this.refreshStats();
    }
    refreshStats() {
        this.identity = this.userService.getIdentity();
        this.token = this.userService.getToken();
        this.stats = this.userService.getStats();
    }

    onSubmit(form) {

        this.publicationService.addNoticia(this.token, this.publication).subscribe(
            response => {
                if (response.publication) {
                    // this.publication=response.publication;
                    form.reset();
                    console.log('success ' + response.publication);
                    console.log(response.publication);

                    this.status = 'success';
                    this.imagen = null;
                    // subir imagen para la publicacion
                    if (this.filesToUpload) {
                        this.uploadService.makeFileRequest(this.urlPublication + 'upload-image/' + response.publication._id, [], this.filesToUpload, this.token, 'image')
                        .then((result: any) => {
                            this.publication.file = result.image;
                        });
                    }
                } else if (!response.publication) {
                    console.log('error' + response.publication);
                    this.status = 'error';
                }
            }, error => {
                console.log(<any>error);
                this.status = 'error';
            }
        );
    }

    sendPublication(event) {
        console.log(event);
        this.sended.emit({ send: 'true' });
    }

    public filesToUpload: Array<File>;
    fileChangeEvent(fileInput: any) {
        this.filesToUpload = <Array<File>>fileInput.target.files;
        this.imagen = this.filesToUpload[0].name;
        console.log(this.filesToUpload[0].name);
    }

    deleteImage(){
        this.filesToUpload = null;
        this.imagen = null;
    }

}
