import { Component, OnInit, DoCheck } from '@angular/core';
import { UserService } from '../../../../service/user.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Global } from '../../../../service/global';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  providers: [UserService]

})
export class HeaderComponent implements OnInit, DoCheck {
  public title: string;
  public test: boolean;
  public admin: boolean;
  public identity;
  public total;
  public last;
  public loged: boolean;
  public url: string;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService
  ) {
    this.title = 'Sistema de Agua Potable';
    this.test = true;
    this.admin = false;
    this.url = Global.url + 'user/';
  }
  ngOnInit() {
    this.identity = this.userService.getIdentity();
    if (this.identity == 'undefined' || this.identity == null) {
      this.identity = null;
      this.loged = false;
      console.log('Ningun usuario Registrado... header');
      this.router.navigate(['/login']);

      return;
    } else if (this.identity != 'undefined' && this.identity != null) {
      this.loged = true;
      if (this.identity.role_user.toLowerCase() == 'admin' || this.identity.role_user.toLowerCase() == 'test') {
        this.admin = true;
      }
      return;
    }
  }
  ngDoCheck() {
    this.identity = this.userService.getIdentity();
    if (this.identity === 'undefined' || this.identity == null) {
      this.identity = null;
      this.loged = false;
      console.log('Ningun usuario Registrado... docheck header');
    } else if (this.identity != 'undefined' && this.identity != null) {
      this.loged = true;
    }
  }

  logout() {
    localStorage.clear();
    this.identity = null;
    this.loged = false;
    console.log('estado de logueo: ' + this.loged);

    this.router.navigate(['/login']);
  }

  showTotal(event) {
    const strings = event.split('.', 2);
    if (event == null || event == undefined) {
      this.total = null;
    } else {
      this.total = strings[0];
      this.last = strings[1];
    }

  }
}
