import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './logins/components/login/login.component';
import { NotFoundComponent } from './client/components/notfound/notfound.component';
import { ImprimirFacturaComponent } from './ImprimirFactura/ImprimirFactura.component';

// Componentes
const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'not-found', component: NotFoundComponent },
  { path: 'factura', component: ImprimirFacturaComponent },
  { path: '**',  redirectTo: 'not-found'},
];

export const appRoutingProviders: any[] = [];
export const routing: ModuleWithProviders = RouterModule.forRoot(routes);
