import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { Meter } from '../models/meter';
import { Global } from './global';

@Injectable()
export class MeterService{
  public url: string;
  public meters;
  constructor(
    private _http: HttpClient
  ) {
    this.url = Global.url + 'meter/';
  }
  // Test de Register()
  // register(user_to_register) {
  //   console.log(user_to_register);
  //   console.log(this.url);
  // }
  // Metodos------------------------------------------------------------------//
  //------Post----------------------------------------------------------------//
  save(token, meter: Meter): Observable<any> {
    let params = JSON.stringify(meter);
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
    return this._http.post(this.url + 'save', params, {headers: headers});
  }
  //------Get-----------------------------------------------------------------//
  showMeters(): Observable<any> {
    let token = JSON.parse(localStorage.getItem('token'));
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
    return this._http.get(this.url + 'show', {headers: headers});
  }
  getMeter(id): Observable<any> {
    let token = JSON.parse(localStorage.getItem('token'));
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
    return this._http.get(this.url + 'get-meter/' + id, {headers: headers});
  }
  getUsersMeters(id): Observable<any> {
    let token = JSON.parse(localStorage.getItem('token'));
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
    return this._http.get(this.url + 'users-meters/' + id, {headers: headers});
  }
  //------Put-----------------------------------------------------------------//
  updateMeter(meter: Meter): Observable<any> {
    let token = JSON.parse(localStorage.getItem('token'));
    let body = JSON.stringify(meter);
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                   .set('Authorization', token);
    return this._http.put(this.url + 'update/' + meter._id, body, { headers: headers })
  } 
  //------Delete--------------------------------------------------------------//
}