import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppMainComponent } from './components/main.component';
import { AddRateComponent } from './components/content/rate/addrate/addrate.component';
import { ShowRatesComponent } from './components/content/rate/showrates/showrates.component';
import { HomeComponent } from './components/content/home/home.component';
import { RegisterSimpleComponent } from './components/content/user/register-simple/register-simple.component';
import { RegisterEmployeesComponent } from './components/content/user/register-employees/register-employees.component';
import { UserListComponent } from './components/content/user/user-list/user-list.component';
import { ProfileComponent } from './components/content/user/profile/profile.component';
import { AddMedidorComponent } from './components/content/medidor/addmedidor/addmedidor.component';
import { GetMedidorComponent } from './components/content/medidor/getmedidor/getmedidor.component';
import { EditMedidorComponent } from './components/content/medidor/editmedidor/editmedidor.component';
import { RegistersComponent } from './components/content/register/registers/registers.component';
import { AddRegisterComponent } from './components/content/register/addregister/addregister.component';
import { ExtrasComponent } from './components/content/rate/extras/extras.component';
import { ImportesComponent } from './components/content/importes/importes.component';
import { ImpuestoMoraComponent } from './components/content/importes/impuesto-mora/impuesto-mora.component';
import { AddFacturaComponent } from './components/content/factura/add-factura/add-factura.component';
import { UserGuard } from '../service/user.guard';
import { ContactComponent } from './components/content/contact/contact.component';

const appClientRoutes: Routes = [
  {
    path: '',
    component: AppMainComponent,
    children: [
      { path: '', redirectTo: 'inicio', pathMatch: 'full' },
      { path: 'inicio', component: HomeComponent, canActivate: [UserGuard] },
      { path: 'nueva-tarifa', component: AddRateComponent },
      { path: 'listar-tarifas', component: ShowRatesComponent },
      { path: 'registro-simple', component: RegisterSimpleComponent },
      { path: 'registro-empleado', component: RegisterEmployeesComponent },
      { path: 'usuarios', component: UserListComponent },
      { path: 'perfil-usuario/:id', component: ProfileComponent },
      { path: 'nuevo-medidor', component: AddMedidorComponent },
      { path: 'ver-medidor', component: GetMedidorComponent },
      { path: 'editar-medidor/:id', component: EditMedidorComponent },
      { path: 'ver-registros', component: RegistersComponent },
      { path: 'facturar', component: RegistersComponent },
      { path: 'nuevo-registro', component: AddRegisterComponent },
      { path: 'extras', component: ExtrasComponent },
      { path: 'importes', component: ImportesComponent },
      { path: 'mora', component: ImpuestoMoraComponent },
      { path: 'contacto', component: ContactComponent },
      { path: 'add-factura/:id', component: AddFacturaComponent }

    ]
  },

];

@NgModule({
  imports: [
    RouterModule.forChild(appClientRoutes)
  ], exports: [
    RouterModule
  ]
})

export class AppClientRoutingModule { }
