import { Component, OnInit, Input, Output, OnChanges } from '@angular/core';
import { MeterService } from '../../../../../service/meter.service';
import { Meter } from '../../../../../models/meter';
import { UserService } from '../../../../../service/user.service';
import { User } from '../../../../../models/user';
import { Rate } from '../../../../../models/rate';
import { Limits } from '../../../../../models/limits';
import { LimitService } from '../../../../../service/limit.service';

import moment from 'moment';
import 'moment/locale/de';
import { Register } from '../../../../../models/register';
import { RegisterService } from '../../../../../service/register.service';
import { ExtraService } from '../../../../../service/extra.service';
import { ErrorCatcher } from '../../../../../models/errorCatcher';
import { ErrorCatcherService } from '../../../../../service/errorCatcher.service';
import { Extra } from '../../../../../models/extra';
import { Location } from '@angular/common';


@Component({
    selector: 'app-add-register',
    templateUrl: './addregister.component.html',
    styleUrls: ['./addregister.component.scss'],
    providers: [MeterService, UserService, LimitService, RegisterService, ExtraService, ErrorCatcherService]

})

export class AddRegisterComponent implements OnInit, OnChanges {

    /**Titulo del Componente @type string */
    public title: string;
    /**Medidor a Ingresar @type Meter */
    public medidor: Meter;
    /**Conjunto de Medidores @type Meter [ ] = [ ] */
    public medidores;
    /**Conjunto de Medidores del usuario Buscado @type Meter [ ] = [ ] */
    public medidoresUser: Meter[] = [];
    /**Registro a Ingresar @type Register */
    public registro: Register;
    /**Registro Anterior @type Register */
    public registroAnterior;
    /**Conjunto de Registros del Medidor @type Retisger [ ] = [ ] */
    public registros: Register[] = [];
    /**Datos de la Tarifa Actual @type Rate */
    public tarifa: Rate;
    /**Conjunto de Limites de la tarifa @type Limits [ ] = [ ] */
    public limites: Limits[] = [];
    /**Conjunto de Usuarios @type User [ ] = [ ] */
    public usuarios: User[] = [];
    /**Resultado de la Busqueda de Usuarios @type User [ ] = [ ] */
    public usersBusq: User[] = [];
    /**Registro de Errores Capturados @type ErrorCatcher */
    public errorCatched: ErrorCatcher;
    /**Interruptor de Usuario Encontrado */
    public encontrado: boolean;
    /**Conteo Usuarios */
    public conteo_usuario;
    /**Clave Medidor */
    public clave_medidor;
    /**Lectura Actual del Medidor */
    public lectura_actual;
    /**Valida si la lectura ingresada refleja un correcto consumo [positivo] */
    public consumo_correcto;
    /**Valores Extras en la aplicacion */
    public extras: Extra[] = [];
    /**Valor Extra en la aplicacion */
    public extra: Extra;
    /**Variable de Confirmacion de Regsitro Completado */
    public completado: boolean;
    /**Variable que envia el Id del medidor para obtener el detalle de los registros */
    public meterId;
    /**Variable de Mensaje en caso de error o alerta en la Fecha */
    public dateMessage;
    /**Variable a manera de Switch para cambiar el formato del mensaje de dateMEssage */
    public dateDanger;
    /**Variable que indica al sistema si es el primer registro de un medidor */
    public nuevoRegistro;
    /**
     * Variable de Error en los registros Imprecindibles para el ingreso de Datos
     */
    public fatalError: boolean;
    /**Variable de tiempo Seleccionado para hacer el Calculo de Diferencia de tiempo
     * @type Date
     */
    public isSelect: Date;
    /**Variable de tiempo Ultimo Registro para hacer el Calculo de Diferencia de tiempo
     * @type Date
     */
    public isRegistered: Date;
    public loading: boolean;

    /** Consturctor, Inicializa o 'Setea' variables */
    constructor(
        private location: Location,
        /**Servicio de Catpura de Errores */
        private errorCatcherService: ErrorCatcherService,
        /**Servicio de Usuario */
        private userService: UserService,
        /**Servicio de Medidor */
        private meterService: MeterService,
        /**Servicio de Registros */
        private _registerService: RegisterService,
        /**Servicio de Limites */
        private limitService: LimitService,
        /** Servicio de Extras */
        public _extraService: ExtraService
    ) {
        this.fatalError = true;
        this.title = 'Cargando......';
        this.initMedidor();
        this.initMedidores();
        this.initErrorCatched();
        this.initUsuarios();
        this.initRegistro();
        this.initExtras();
        this.loading = true;
    }
    /** Se carga al inicar el componente */
    ngOnInit() {
        this.title = 'Ingresar la lectura del Medidor'
    }
    return() {
      this.location.back();
    }
    /**
     * Al detectar un Cambio en la Aplicacion se ejecutan las acciones dentro de la funcion
     * @see change
     */
    ngOnChanges() {

    }
    /**
     * Inicializar los registros de Extras
     */
    initExtras() {
        this._extraService.getExtra().subscribe(
            response => {
                this.extras = response.extra;
                if (response.extra.length <=0) {
                    this.fatalError = true;
                    this.loading = false;
                    alert('no existen los datos de RMUV... por favor ingresarlos antes de continuar con los registros de consumo')
                } else if (response.extra.length >=1) {
                    this.fatalError = false;
                    this.loading = false;
                }
            }, error => {
                this.loading = false;
                console.log( error as any);
                this.errorCatched.description =  error.toString() as any;
                this.errorCatched.table = 'Extra';
                this.errorCatched.action = 'getExtras';
                this.errorCatched.title = 'Error en la Obtencion de Los datos Extras';
                this.errorCatched.zone = 'addRegister';
                this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
                this.errorCatcherService.saveError(this.errorCatched).subscribe(
                    response => {
                        alert(response.Message);
                    });
            }
        );
    }
    /**
     * Inicializar el objeto medidores [Meter] con el registro completo de medidores en la base de datos
     * @function Load
     */
    initMedidores() {
       this.meterService.showMeters().subscribe(
           response => {
               this.medidores = response.Medidores;
           }, error => {
                console.log( error as any);
                this.errorCatched.description =  error.toString() as any;
                this.errorCatched.table = 'Meter';
                this.errorCatched.action = 'showMeters';
                this.errorCatched.title = 'Error en la Obtencion de Medidores';
                this.errorCatched.zone = 'addRegister';
                this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
                this.errorCatcherService.saveError(this.errorCatched).subscribe(
                    response => {
                        alert(response.Message);
                    });
           });
    }
    /**
     * Inicializar los Usuarios [User[]=[]]
     * @function Load
     */
    initUsuarios() {
       this.userService.showRaw().subscribe(
        response => {
         this.usuarios = response.Usuarios;
        }, error => {
         console.log( error as any);
         this.errorCatched.description =  error.toString() as any;
         this.errorCatched.table = 'User';
         this.errorCatched.action = 'getUsers';
         this.errorCatched.title = 'Error en la Obtencion de Usuarios';
         this.errorCatched.zone = 'addRegister';
         this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
         this.errorCatcherService.saveError(this.errorCatched).subscribe(
             response => {
                 alert(response.Message);
             });
        });
    }
    /**
     * Inicializar el objeto medidor [Meter]
     * @function init
     */
    initMedidor() {
       this.medidor = {
            _id: '',
            clave: null,
            direccion: '',
            updated_at: '',
            user: '',
            rate: '',
            edited_by: '',
            image: ''
       };
    }
    /**
     *  Inicializar el objeto errorCatched [ErrorCatcher]
     * @function init
     */
    initErrorCatched() {
        this.errorCatched = new ErrorCatcher (
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            false,
            '',
            ''
        );
    }
    /**
     * Inicializar el objeto de registro
     * @function init
     */
    initRegistro() {
        this.registro = new Register(
            null,             // id
            0,              // lectura
            0,              // consumo
            0,              // year
            0,              // month
            0,              // excessconsumo
            0,              // excesscosto
            0,              // excessm3
            0,              // base
            0,              // subtotal
            '',             // updated_at
            false,          // cancelado
            '',             // registered_by
            '',             // meter
            ''              // extra
        );
    }
    /**Devolver los Limites de la tarifa del medidor */
    initLimits(rate) {
        this.limitService.getLimits(rate).subscribe(
            response => {
                this.limites = response.Limits;
                this.limites.sort((b, a) => (b.limit_from - a.limit_from));
            }, error => {
                console.log( error as any);
                this.errorCatched.description =  error.toString() as any;
                this.errorCatched.table = 'Limit';
                this.errorCatched.action = 'GetLimits';
                this.errorCatched.title = 'Error en la Obtencion de los Limites';
                this.errorCatched.zone = 'addRegister';
                this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
                this.errorCatcherService.saveError(this.errorCatched).subscribe(
                    response => {
                        alert(response.Message);
                    });
            }
        );
    }
    /**Busqueda del medidor [clave] dentro de los mediores [meters]
     * almacenados de Forma Local
     * @param clave
     */
    FindMeter(clave) {
        this.limpiar();
        let conteo = 0;
        this.initMedidor();
        this.initRegistro();
        if (this.medidores) {
            for (let index = 0; index < this.medidores.length; index++) {
                const element = this.medidores[index];
                if (clave == element.clave) {
                    this.medidor = element;
                    this.meterId = element._id;
                    this.registro.meter = element._id;
                    // this.loadRegisters(this.medidor._id);
                    this.tarifa = element.rate;
                    this.registro.base = this.tarifa.base;
                    this.initLimits(this.tarifa._id);
                    this.clave_medidor = element.clave;
                    conteo++;
                    this.loadRegisters(this.medidor._id);
                }
            }
            if (conteo == 1) {
                this.encontrado = true;
            } else if (conteo == 0 && clave.length > 5) {
                this.encontrado = false;
            } else if (conteo == 0 && clave.length < 6) {
                this.encontrado = null;
            }
        }
    }

    /**
     * Funcion de Busqueda de Usuario segun la categoria
     * @param Cedula_Nombres
     */
    busquedaUsuario(termino, seleccion) {
        termino = termino.toLowerCase();
        termino = termino.replace(/ /g, '');
        this.limpiar();
        let count = +0;
        let busqueda;
        this.medidoresUser = [], [];
        this.usersBusq = [], [];
        for (let index = 0; index < this.usuarios.length; index++) {
            const element = this.usuarios[index];
            switch (seleccion) {
                case 'nombres':
                    busqueda = element.nombre.replace(/ /g, '').toLowerCase() + element.apellido.replace(/ /g, '').toLowerCase();
                    if (busqueda.indexOf(termino, 0) >= 0) { this.usersBusq.push(element), count++; }
                    break;

                case 'cedula':
                    busqueda = element.cedula.replace('-', '');
                    termino = termino.replace('-', '');
                    if (busqueda.indexOf(termino, 0) >= 0) { this.usersBusq.push(element), count++; }
                    break;
                default:
                    count = -1;
                    break;
            }
        }

        if ( count < 1) {
            this.usersBusq = null;
        }
        this.conteo_usuario = count;
    }

    /**Busqueda de los Medidores del Usuario
     * @var medidoresUser
     */
    getMeters(user_id) {
        this.medidoresUser = [], [];
        for (let index = 0; index < this.medidores.length; index++) {
            const element = this.medidores[index];
            if (element.user && element.user._id == user_id) {
                this.medidoresUser.push(element);
            }
        }
    }

    /**Devuelve la Fecha del DatePicker
     * @param $event
     * selectedMonthAsText = moment().month(event.monthIndex).format('MMMM');
     */
    getDate(event: {year: number, monthIndex: number}) {
        this.registro.year = event.year;
        this.registro.month = event.monthIndex;
        this.isSelect = new Date (this.registro.year, this.registro.month);
        this.valiDate();
    }
    /**Devuelve los registros completos del medidor */
    loadRegisters(id) {
        this.dateMessage = null;
        this._registerService.getRegisters(id).subscribe(
            response => {
                if (!response.registers) {
                    this.isRegistered = null;
                    this.registroAnterior = null;
                    this.nuevoRegistro = true;
                    this.registros = null;
                } else {
                    console.log(response);
                    this.registros = response.registers;
                    this.registros.sort((a, b) => (b.month - a.month));
                    this.registros.sort((a, b) => (b.year - a.year));
                    this.registroAnterior = this.registros[0];
                    this.registroAnterior._id = null;
                    this.isRegistered = new Date (this.registroAnterior.year, this.registroAnterior.month);
                    this.valiDate();
                }
            }
        );
    }
    /**Comprobacion en caso de que la fecha actual sea menor o igual a la ultima fecha ingresada */
    valiDate() {
        console.log('validando...');
        this.dateMessage = null;
        this.dateDanger = null;
        if (this.nuevoRegistro == true || this.registros == null) { this.registroAnterior = null; }
        if (this.registro && this.registroAnterior != null) {
            const fecha1 = moment(this.isRegistered);
            const fecha2 = moment(this.isSelect);
            const Diferencia = fecha2.diff(fecha1, 'months');
            if (Diferencia == 1) {
                console.log('todo correcto');
                this.dateMessage = null;
                this.dateDanger = null;
            } else if (Diferencia == 2) {
                alert('No se han realizado registros del mes Anterior');
                this.dateDanger = false;
                this.dateMessage = 'No se han realizado registros del mes Anterior';
            } else if (Diferencia < 1) {
                alert('ya se ha registrado esta fecha');
                this.dateMessage = 'ya se ha registrado esta fecha';
                this.dateDanger = true;

            } else if (Diferencia > 2) {
                alert('No se han realizado registros desde hace ' + (Diferencia - 1) + ' Meses');
                this.dateDanger = false;
                this.dateMessage = 'No se han realizado registros desde hace ' + (Diferencia - 1) + ' Meses';
            }
        }
    }
    /**Calcular Consumo */
    consumo(lectura: number) {
        this.completado = false;
        this.consumo_correcto = null;
        this.registro.lectura = lectura;
        let consumo: number;
        if (this.nuevoRegistro) {
            consumo = lectura;
        } else {
            consumo = lectura - this.registroAnterior.lectura;
        }
        if (consumo > 0) {
            this.consumo_correcto = true;
            const exceso = consumo - this.limites[0].limit_to;
            this.registro.consumo = consumo;
            if (exceso <= 0) {
                this.registro.excessConsumo = 0;
                this.registro.excessCosto = 0;
                this.registro.excessM3 = 0;
                this.Costos(0);
                console.log('sin exceso');
            } else if (exceso > 0) {
                this.registro.excessConsumo = exceso;
                this.limite(consumo);
            }
        } else if (consumo == 0) {
            console.log('sin consumo');
            this.registro.consumo = consumo;
            this.Costos(0);
            this.consumo_correcto = true;
        } else if (consumo < 0) {
            console.log('lectura incorrecta');
            this.consumo_correcto = false;
        }

    }
    /** Calculo de Límite actual de consumo: Algoritmo de limites
     * si el limite superior [limit_to] es nulo [null] o [undefined] se asigna un valor [999999999];
     * si [Consumo] actual es mayor o igual [>=] al limite inferior [limit_from] y.-
     * si [Consumo] actual es menor o igual [<=] al limite superior [limit_to]
     * exceso(costo por m3)->[excessCosto] = [rmuv]=>[extra.rmuv] * excedente del limite [limit.excess]
     * @param consumo
     */
    limite(consumo) {
        console.log('calculando exceso del consumo ' + consumo);
        for (let index = 0; index < this.limites.length; index++) {
            const rango = this.limites[index];

            if (typeof rango.limit_to != 'number') {  rango.limit_to = 999999999; }
            if (consumo >= rango.limit_from && consumo <= rango.limit_to) {
                console.log( rango.limit_from , ' ' , rango.limit_to);
                this.registro.excessM3 = this.extras[0].rmuv * (rango.cost / 100); // Corregir esta vaina
                this.Costos(this.registro.excessM3);
            }
        }
    }
    /**Devuelve los valores de costos.
     * Costo total del excedido->[excessCosto] = [excessM3] * [excessConsumo]
     * costo->[subtotal] = [base] + [excessCosto]
     */
    Costos(excesoM3) {
        this.registro.excessCosto = excesoM3 * this.registro.excessConsumo;
        this.registro.subtotal = this.registro.base + this.registro.excessCosto;
        this.completado = true;
    }
    /**Funcion para Guardar los datos del registro actual */
    onSubmit() {
        console.log('guardando registro');
        console.log(this.registro);

        this.registro.extra = this.extras[0]._id;
        this._registerService.save(this.registro).subscribe(
            response => {
                console.log(response);
                alert(response.Message);
                this.clave_medidor = null;
                this.limpiar();
            }, error => {
                console.log( error as any);
                setTimeout(() => {
                    this.errorCatched.description =  error.toString() as any;
                    this.errorCatched.table = 'Registers';
                    this.errorCatched.action = 'saveRegisters';
                    this.errorCatched.title = 'Error al Guardar el Registro';
                    this.errorCatched.zone = 'addRegister';
                    this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
                    this.errorCatcherService.saveError(this.errorCatched).subscribe(
                        response => {
                            alert(response.Message);
                        });
                }, 500);
            }
        );
    }

    /**Funcion para Limipiar los datos Ingresados Anteriormente */
    limpiar() {
        this.nuevoRegistro = null;
        this.completado = false;
        this.registroAnterior = null;
        this.limites = [], [];
        this.lectura_actual = null;
        this.initMedidor();
        this.initRegistro();
        this.encontrado = null;
        this.isRegistered = null;
    }
}
