import { Injectable } from '@angular/core';
import { Limits } from '../models/limits';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';


@Injectable()
export class LimitService {
    public url: string;
    public limit: Limits;

    constructor(
        private http: HttpClient
    ) {
        this.url = Global.url + 'limits/';
    }
    // Metodos------------------------------------------------------------------//
    // ------Post----------------------------------------------------------------//
    save(limit, id): Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const params = JSON.stringify(limit);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this.http.post(this.url + 'save/' + id , params, { headers });
    }
    // ------Get----------------------------------------------------------------//
    getLimits(rate): Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this.http.get(this.url + 'show/' + rate, { headers});
    }
    getAllLimits(): Observable<any> {
        const token = JSON.parse(localStorage.getItem('token'));
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
        return this.http.get(this.url + 'show-all/', { headers});
    }
    // ------Get----------------------------------------------------------------//
    /**
     * Funcion para Borrar todos los limites de la tarifa
     * @param id
     */
    deleteLimits(id): Observable<any> {
      const token = JSON.parse(localStorage.getItem('token'));
      const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                     .set('Authorization', token);
      return this.http.delete(this.url + 'delete/' + id, { headers});
  }
}
