import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { Global } from './global';
import { Message } from '../models/message';

@Injectable()
export class MessageService {
  public url: string;
  public message;
  constructor(
    private _http: HttpClient
  ) {
    this.url = Global.url + 'message/';
  }

  addMessage(token, message): Observable<any> {
    let params = JSON.stringify(message);
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this._http.post(this.url + 'save', params, { headers: headers });
  }

  getMyMessage(token, page): Observable<any> {
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this._http.get(this.url + 'show-received/' + page, { headers: headers });
  }
  getEmittMessage(token, page): Observable<any> {
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this._http.get(this.url + 'show-emitted/' + page, { headers: headers });
  }

}

