import { Component } from '@angular/core';
import { User } from '../../../../../models/user';
import { Meter } from '../../../../../models/meter';


@Component({
    selector: 'app-profile-medidor',
    templateUrl: './profilemedidor.component.html',
    styleUrls: ['./profilemedidor.component.scss']
})
export class ProfileMedidorComponent {
    public title: string;
    public conteos;
    public usuarios;
    public medidores;
    public users: User[] = [];
    public meters: Meter[] = [];
    public meter: Meter;

    constructor() {
        this.title = 'Detalles del Medidor';
    }

    busqueda(termino, categoria) {
        console.log('Termino: ' + termino + '. Categoria: ' + categoria);
        this.conteos = null;

        if (categoria == 'clave') {
            console.log('dentro de clave');

        } else if (categoria == 'cedula') {
            console.log('dentro de cedula');
        }

    }
}
