import { Component, Output, EventEmitter, OnInit, Input, OnChanges } from "@angular/core";
import { MoraService } from '../../../../../../service/mora.service';
import { Mora } from '../../../../../../models/mora';
import { Observable } from 'rxjs';


@Component({
    selector: 'app-show-impuesto-mora',
    templateUrl: './show-impuesto-mora.component.html',
    styleUrls: ['./show-impuesto-mora.component.scss'],
    providers: [MoraService]
})
export class ShowImpuestoMoraComponent implements OnInit, OnChanges {
    public moras: Mora[]=[];
    @Output() moraEdit= new EventEmitter();
    @Output() noLimit= new EventEmitter();

    constructor(
        private moraService: MoraService
    ) {

    }

    ngOnChanges(){
        this.initMoras();
    }
    ngOnInit() {
        this.initMoras();

    }
    initMoras() {
        this.moraService.gets().subscribe(
            response => {
                this.moras = response.Mora;
            }, error => {
                console.error(error as any);
            }
        )
    }

    onEdit(mora) {
        mora = JSON.stringify(mora);
        this.moraEdit.emit(mora);
    }
    onReload() {
        this.initMoras();
    }
    onDelete(id) {
        let deleteMora = confirm('Desea borrar este Impuesto por Mora?. Recuerde que una vez borrados, los datos no pueden volver a recuperarse.');
        if (deleteMora === true) {
            this.moraService.deleteMora(id).subscribe(
                response => {
                    alert(response.Message);
                    this.initMoras();
                    this.noLimit.emit(true);
                }, error => {
                    console.error(error as any);
                }
            )
        }
    }
}
