import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { Global } from './global';
import { Publication } from '../models/publication';
// import { } from '../../../../api/routes/publication' ;
@Injectable()
export class PublicationService {
    public url: string;
    public publications;
    constructor(
        private _http: HttpClient
    ) {
        this.url = Global.url + 'publication/';
    }
    addPublication(token, publication): Observable<any> {
        let params = JSON.stringify(publication);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url + 'save', params, { headers: headers });
    }
    addNoticia(token, publication): Observable<any> {
        let params = JSON.stringify(publication);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url + 'save-noticia', params, { headers: headers });
    }
    getPublication(token, page): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', token);
        return this._http.get(this.url + 'paginate/'+page, { headers: headers });
    }
    getPublications(token): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', token);
        return this._http.get(this.url + 'show-raw/', { headers: headers });
    }
    getPublicationUser(token, user_id, page = 1): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', token);
        console.log(this.url + 'show/' + user_id + '/' + page,);

        return this._http.get(this.url + 'show/' + user_id + '/' + page, { headers: headers });
    }
    deletePublication(token, id): Observable<any> {
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', token);
        return this._http.delete(this.url + 'delete/'+id, { headers: headers });
    }
}

